.Produit{
    gap: 1rem;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: burlywood;
    box-shadow: 5px 5px 15px 2px rgba(80, 80, 80, 0.7);
    
    button{
        margin: 5px;
        background-color: black;
        padding: 1rem;
        color:white;
        border: 1px solid #555;
        box-shadow: 2px 2px 10px 1px rgba(80, 80, 80, 0.7);
        cursor: pointer;
    }
}